}

.navigation__item {
  height: 70px;
  min-width: 83px;
  align-items: center;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.2;
  display: flex;
  position: relative;
}

.navigation__item:hover button, .navigation__item button:focus {
  font-weight: 600;
}

.navigation__item:hover:after {
  z-index: 3;
  content: "";
  width: 100%;
  height: 3px;
  background-color: #83b0e5;
  border-radius: 0 0 3px 3px;
  position: absolute;
  top: 70px;
}

