  fill: #83b0e5;
}

.navigation__dropdown-wrap {
  z-index: 2;
  background-color: #fff;
  border: 1px solid #7d7d7d;
  border-radius: 0 0 5px 5px;
  position: absolute;
  top: 100%;
}

.user-menu.navigation__dropdown-wrap {
  transform: translateX(-80%);
